.main_page {
  /* background-color: blue; */
  width: 100%;
  margin-top: 10vh;
  height: 90vh;
}

.table {
  width: 70%;
  /* background: green; */
  display: table;
  margin-left: 15%;
  margin-top: 1%;
  font-size: 95%;
}

.export_button {
  height: 30px;
  width: 10%;
  border-style: none;
  /* box-shadow: 10px 2px 1px rgba(0, 0, 0, 0.116); */
  cursor: pointer;
  color: white;
  background: #098fa6;
  border-radius: 5px;
  margin-top: 1%;
  margin-left: 15%;
}
th {
  color: rgb(86, 86, 86);
}

.remark_container {
  width: 100%;
  height: 4%;
  /* background-color: rgba(0, 128, 0, 0.275); */
  position: sticky;
  display: flex;
  justify-content: center;
  gap: 0.2%;
}
.remark_button {
  width: 50px;
  cursor: pointer;
  color: white;
  background: #098fa6;
  border-radius: 5px;
  border-style: none;
  text-align: center;
}
