.employee-card {
  display: flex;

  flex-direction: column;

  border-radius: 5px;
  height: 280px;
  width: 180px;
  align-items: center;

  box-shadow: 0 1px 1px rgba(74, 72, 72, 0.33);
  line-height: 0.5px;
  transition: box-shadow 0.5s ease-in-out;
  font-size: 12px;
}

.employee-card:hover {
  box-shadow: 0 5px 5px rgba(0, 0, 0, 0.19);

  cursor: pointer;
}

.imageDisplay {
  width: 90px;
  height: 110px;
  border-radius: 20px;
  object-fit: cover;
}

.control {
  margin-top: 20px;
  display: flex;
  /* position: absolute; */

  left: 0;
  bottom: 0%;
  justify-content: space-evenly;

  position: flex-end;
}
.updateBtn {
  background-color: lightblue;
}
.updateBtn:hover {
  background-color: #098fa6;
  color: white;
  cursor: pointer;
}
.updateBtn,
.deleteBtn,
.payslipBtn {
  border: none;
  width: 45px;
  height: 25px;
}

