@import url('https://netdna.bootstrapcdn.com/font-awesome/4.4.0/css/font-awesome.css');
/* @import url(https://fonts.googleapis.com/css?family=Lato:400, 300, 700); */

.di {
  width: 100%;
  height: 100%;
  /* background-color: rgba(87, 4, 4, 0.174); */
  /* background-image: url('../../public/images/nice2.jpg'); */
  background-image: url('../../public/images/bbb.jpg');
  /* background: rgba(173, 173, 173, 0.124); */
  background-size: cover;
  background-position: 20%;
  background-repeat: no-repeat;
}

.login_form_container {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(173, 173, 173, 0.322);
}

.form_login_container {
  margin-top: 5%;
  width: 30%;
  height: 70%;

  padding: 0.5%;
  border-radius: 4px;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* background-color: aqua; */
  position: relative;
}

p {
  position: relative;
  left: flex-start;
}

.login_input {
  height: 40%;
  width: 100%;
  /* margin-left: %; */
  /* background: green; */
}

.login_submit {
  margin-top: 5%;
  width: 100%;
  height: 9%;
  cursor: pointer;
  border-style: none;
  background: #098fa6;
  color: white;
}

.login_submit:active {
  transition: background-color 1 ease-in-out;
}
.login_submit:hover {
  background: rgb(0, 133, 169);
}

.divCont {
  width: 100%;
  height: 20%;
  /* background: yellow; */
}

.reg {
  margin-top: 10%;
}
