.content_page {
  margin-top: 10vh;
  height: 90vh;
  width: 100%;
  /* background-color: green; */
  display: grid;

  grid-template-columns: auto auto auto auto;

  justify-content: space-evenly;

  font-family: 'Fondamento', cursive;
  font-family: 'Poppins', sans-serif;

  animation: fadeInAnimation ease 2s;
  animation-iteration-count: 1;
  animation-fill-mode: both;
}

@keyframes fadeInAnimation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.serverButton {
  width: 80px;
  height: 20px;

  background-color: #098fa6;

  border-radius: 5px;
  cursor: pointer;
  border: none;

  bottom: 15px;
  display: flex;
  gap: 10px;
  align-items: center;
  justify-content: center;
  color: white;
  text-align: center;
}

.pagination {
  display: flex;
  position: absolute;
  bottom: 0%;
  margin-left: 1%;

  gap: 2%;
}
